<template>
  <v-container class="fill-height" justify-center fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12" v-if="0===state">
          <v-card-text>Verifying</v-card-text>
        </v-card>
        <v-card class="elevation-12" v-if="1===state">
          <v-card-text>
            Your email has been verified. You can login to our service now.
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn text color="primary" to="/login">Go to login screen</v-btn>
          </v-card-actions>
        </v-card>
        <v-card class="elevation-12" v-if="2===state">
          <v-card-text>
            <strong>Invalid token!!!</strong>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { verify } from "@/lib/auth"

export default {
  name: "Verify",
  data: () => ({
    state: 0
  }),
  mounted: async function() {
    let response = await verify(this.$route.params.token)
    if (response.code === 200) {
      this.state = 1
    } else {
      this.state = 2
      console.log(response)
    }
  },
}
</script>
